<template>
    <div class="loading-container">
        <div class="loading-backdrop"></div>
        <div class="loading">
            <img :src="src"><div>{{message}}</div>
        </div>
    </div>
</template>

<script>

export default {
  name: 'BlockUI',
  props : ['message', 'src']
}
</script>

<style lang="scss" scoped>
.loading-container {
  .loading-backdrop {
    position: fixed;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    background-color: lightgray;
    opacity: 0.5;
    z-index: 9998;
  }

  .loading {
    z-index: 9999;
    position: fixed;
    text-align: center;
    left: calc((100% - 202px)/2);
    top: calc((100% - 200px)/2);
    background-color: white;
    padding: 20px 50px;
    border-radius: 12px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  }
}
</style>



